import "./Description.css";
import description1 from "../../img/description1.png";
import description2 from "../../img/description2.png";
import description3 from "../../img/description3.png";

export default function Description() {
    return (
        <section className="description" name="description" id="description">
        <div className="description__container">
            <div className="description__header">
                <h2 className="description__title">  </h2>
                <button className="description__button"> </button>
            </div>
            <div className="description__content">
                <div className="description__item">
                <img className="description__icon" src={description1} alt="   "/>
                <div className="description__text">   </div>
            </div>
            <div className="description__item">
                <img className="description__icon" src={description2} alt="    "/>
                <div className="description__text">    </div>
            </div>
            <div className="description__item">
                <img className="description__icon" src={description3} alt="  "/>
                <div className="description__text">  </div>
            </div>
            </div>
        </div>
    </section>
    );
}
